// 关于登录的相关接口请求
import request from '@/utils/request.js'

// 1.获取图像验证码
export const getPicCode = () => {
  return request({
    url: '/captcha/image'
  })
}

// 2.获取短信验证码
export const getMsyCode = (code, key, mob) => {
  return request({
    url: '/captcha/sendSmsCaptcha',
    method: 'POST',
    data: {
      form: {
        captchaCode: code,
        captchaKey: key,
        mobile: mob
      }
    }
  })
}

// 3.获取手机验证码
export const CodeLogin = (mobile, smsCode) => {
  return request({
    url: '/passport/login',
    method: 'POST',
    data: {
      form: {
        isParty: false,
        partyData: {},
        mobile,
        smsCode
      }
    }
  })
}
