<template>
    <div class="login">
      <!-- 头部 -->
      <van-nav-bar title="会员登录" left-arrow @click-left="$router.back()"></van-nav-bar>
      <!-- 底部 -->
      <div class="container">
        <!-- 提示字 -->
        <div class="title">
          <h3>手机号登录</h3>
          <p>未注册的手机号登录后将自动注册</p>
        </div>
        <!-- 三个输入框 -->
        <div class="form">
          <div class="form-item">
            <input v-model="mobile" type="text" placeholder="请输入手机号码">
          </div>
          <div class="form-item">
            <input v-model="picCode" type="text" placeholder="请输入图形验证码">
            <img v-if="picUrl" @click="getPicCode" :src="picUrl" alt="">
          </div>
          <div class="form-item">
            <input v-model="msgCode" type="text" placeholder="请输入短信验证码">
            <button  ref="btnCode" @click="getCode">
              {{ totalSecond === second ? '获取验证码' : second + '秒后重新发送'}}
            </button>
          </div>
        </div>
        <!-- 登录按钮 -->
        <div class="login-btn">
          <button @click="login">登录</button>
        </div>
      </div>
    </div>
</template>

<script>
import { getPicCode, getMsyCode, CodeLogin } from '@/api/login.js'
import { Toast } from 'vant'
import { mapMutations } from 'vuex'

export default {
  name: 'LoginIndex',

  data () {
    return {
      picKey: '', // 将来请求传递的图形验证码唯一标识
      picUrl: '', // 存储请求渲染的图片地址
      totalSecond: 60, // 总秒数
      second: 60, // 当前描述
      timer: null, // 定时器
      mobile: '', // 用户输入手机号
      picCode: '', // 用户输入的图形验证码
      msgCode: ''// 短信验证码
    }
  },
  created () {
    this.getPicCode()
  },
  methods: {
    ...mapMutations('user', ['setUSerInfo']),
    // 获取图形验证码
    async getPicCode () {
      const { data: { base64, key } } = await getPicCode()
      // Toast('你好')
      this.picUrl = base64
      this.picKey = key
    },

    // 校验手机号和验证码的合法
    valdFn () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        Toast('请输入正确的手机号码')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        Toast('请输入正确的验证码')
        return false
      }
      return true
    },

    // 获取短信验证码
    async getCode () {
      if (!this.timer && this.second === this.totalSecond) {
        if (!this.valdFn()) {
          return
        }

        // 获取信息验证码
        const res = await getMsyCode(this.picCode, this.picKey, this.mobile)
        console.log(res)
        Toast('发送成功,注意查收')

        // 倒计时
        this.timer = setInterval(() => {
          this.second--
          if (this.second <= 0) {
            clearInterval(this.timer)
            this.timer = null
            this.second = this.totalSecond
          }
        }, 1000)
      }
    },

    // 登录验证
    async login () {
      if (!this.valdFn()) {
        return
      }
      if (!/^\d{6}$/.test(this.msgCode)) {
        Toast('请输入正确的验证码')
        return
      }
      const res = await CodeLogin(this.mobile, this.msgCode)
      console.log(res)
      Toast('登录成功')
      this.setUSerInfo(res.data)
      const routerUrl = this.$route.query.backUrl || '/'
      this.$router.replace(routerUrl)
    },

    // 离开页面清除
    destroyed () {
      clearInterval(this.timer)
    }
  }
}
</script>

<style scoped>
.container{
  width: 100%;
  padding: 35px 25px;
}

.title{
  font-size: 24px;
  height: 100px;
}

.title p{
  margin: 10px 0;
  font-size: 16px;
  color: #c8c8c8;
}

.form{
  width: 100%;
  height: 250px;
}

.form-item{
  width: 100%;
  height: 33%;
  display: flex;
  align-items: center;
  padding:0 3%;
}

.form-item input{
  height: 40%;
  border: none;
  border-bottom:1px solid #c1c1c1;
  flex: 1;
  font-size: 16px;
}

.form-item img{
  height: 40%;
}

.form-item button{
  height: 30%;
  margin: 15px 0;
  border: none;
  background-color:transparent;
  color: orange;
  font-size: 14px;
}

.login-btn{
  width: 100%;
  height: 50px;
  text-align: center;
}
.login-btn button{
  width: 100%;
  border-radius: 35px;
  height: 80%;
  border: 1px solid #000;
  font-size: 18px;
  color: white;
  background-color: orange;
  margin-top: 15px;
}

</style>
